/**
 * Analyst Ratings Types
 * Defines ratings and recommendations for various asset types
 */

export type RatingAssetType = 'stock' | 'crypto' | 'etf' | 'index' | 'bond' | 'commodity' | 'forex' | 'mutual_fund'

export type RatingScale = '1-5' | '1-10' | 'strong_weak' | 'buy_hold_sell' | 'a_f' | 'percent'

export interface AnalystRating {
  id: string
  symbol: string
  asset_type: RatingAssetType
  rating_source: string
  rating_value: number | string
  rating_scale: RatingScale
  target_price?: number
  target_price_high?: number
  target_price_low?: number
  recommendation: 'strong_buy' | 'buy' | 'hold' | 'sell' | 'strong_sell' | 'neutral'
  confidence_score?: number
  analyst_name?: string
  report_url?: string
  published_at: string
  last_updated: string
}

export interface RatingConsensus {
  symbol: string
  asset_type: RatingAssetType
  rating_count: number
  average_rating: number
  min_rating: number
  max_rating: number
  median_rating: number
  rating_distribution: {
    strong_buy: number
    buy: number
    hold: number
    sell: number
    strong_sell: number
  }
  average_target_price?: number
  target_price_high?: number
  target_price_low?: number
  upside_potential?: number
  consensus_recommendation: 'strong_buy' | 'buy' | 'hold' | 'sell' | 'strong_sell' | 'neutral'
  last_updated: string
}

export interface RatingTrend {
  symbol: string
  period: '1w' | '1m' | '3m' | '6m' | '1y'
  previous_consensus: 'strong_buy' | 'buy' | 'hold' | 'sell' | 'strong_sell' | 'neutral'
  current_consensus: 'strong_buy' | 'buy' | 'hold' | 'sell' | 'strong_sell' | 'neutral'
  trend_direction: 'up' | 'down' | 'stable'
  rating_change_count: number
  average_rating_change: number
}

export interface RatingDetails {
  analyst: string
  firm: string
  rating: string
  previous_rating?: string
  target_price: number
  previous_target_price?: number
  time_on_rating: number
  accuracy_score?: number
  reports_count: number
  specialties?: string[]
}

export interface CryptoRating {
  id: string
  symbol: string
  name: string
  trust_score: number
  trust_score_direction: 'up' | 'down' | 'stable'
  developer_score: number
  community_score: number
  liquidity_score: number
  market_score: number
  public_interest_score: number
  consensus_grade: 'a' | 'b' | 'c' | 'd' | 'e' | 'f'
  last_updated: string
}

export interface EtfRating {
  id: string
  symbol: string
  name: string
  overall_rating: number
  risk_rating: number
  return_rating: number
  expense_rating: number
  liquidity_rating: number
  diversification_rating: number
  sharpe_ratio: number
  expense_ratio: number
  aum: number
  last_updated: string
}

export interface BondRating {
  id: string
  symbol: string
  name: string
  moody_rating?: string
  sp_rating?: string
  fitch_rating?: string
  composite_rating: string
  outlook: 'positive' | 'negative' | 'stable'
  last_updated: string
}

export interface RatingSummary {
  symbol: string
  asset_type: RatingAssetType
  consensus: RatingConsensus
  ratings: AnalystRating[]
  trends: RatingTrend[]
  rating_details?: RatingDetails[]
  crypto_details?: CryptoRating
  etf_details?: EtfRating
  bond_details?: BondRating
  last_updated: string
}

export interface RatingFilter {
  asset_type?: RatingAssetType
  recommendation?: AnalystRating['recommendation']
  rating_source?: string
  min_confidence?: number
  sort_by?: 'rating' | 'target_price' | 'published_at' | 'confidence'
  sort_order?: 'asc' | 'desc'
  limit?: number
}

export interface RatingAction {
  type: 'upgrade' | 'downgrade' | 'initiate' | 'maintain' | 'coverage_dropped'
  previous_rating?: string
  new_rating: string
  effective_date: string
}

